@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

.main_container{
    

  background-color: #fff;
  flex: 1 1 auto;
   
}
.main_header{
    display: flex;
    width: 100%;
    height: 400px;
    background-color: #6C5EFE;
   
    align-items: center;
    
}
.main_header_text{
    margin-left: 50px;
color:white;

font-size: 60px;
}
.main_header_img{
 
    flex: content;
    margin-right: 100px;


}

.main_header_img_item{
    float: right;
 width:300px;
 color:#6C5EFE;
 


}
/* ---------------------------------------------------------------------------------------------------------------- */
.main_content{

}
.main_nav{
    margin-top: 20px;
display: flex;
justify-content: space-around;
align-items: center;
}

.main_notes{
    display: flex;
align-items: center;
flex-direction: column;
}
.main_notes p{
    margin-top: 20px;
    font-size: 25px;
}
.main_notes_img{
    width: 150px;
}

.main_example{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.main_example p{
    margin-top: 20px;
    font-size: 25px;
}
.main_example_img{
    width: 150px;
}

.main_shop{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.main_shop_img{
    width: 150px;
}
.main_shop p{
    margin-top: 20px;
    font-size: 25px;
}

@media only screen and (max-width:768px){
    .main_header_text{
        margin-left: 50px;
    color:white;
    
    font-size: 40px;
    }
    
    
    .main_header{
    
     width:100%;
}
}