.nav_i{
     grid-area: nav;
}
.content_notes{   
    margin-top: 30px;
    grid-area: content;
    
   display: grid;
   grid-template-columns: 1fr;
   
 
}
.notes_item_list{
    background-color: white;
    width: 100%;
   
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr ;
    justify-items: center;
   

}
.notes_item_main{
    font-size: 30px;
    font-weight: 800;
    color: rgb(149, 101, 101);
    border-radius: 10px;
    margin: 20px;
    width: 200px;
    height: 200px;
    background-color: #fff;
    border: 2px solid #6C5EFE;
    text-align: center;
    opacity: 0.5;
   
}

@media only screen and (max-width: 768px){
    .nav_i{
      display: none;
    }
    .notes_item_list{  
        background-color: #fff;
        margin: 0 auto;
      
       display: grid;
       grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr ;
      justify-items: center;
      column-gap: 15px;
    }
    .container_notes{
  
        width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 611px;
        grid-template-areas: 
        "content";
        justify-items: center;
    }
}

@media only screen and (max-width: 400px){
    .nav_i{
      display: none;
    }
    .notes_item_list{  
        background-color: #fff;
        margin: 0 auto;
       
       display: grid;
       grid-template-columns: 1fr;
      grid-template-rows: 1fr auto;
      justify-items: center;
      column-gap: 15px;
    }
    .container_notes{
  
        width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 611px;
        grid-template-areas: 
        "content";
        justify-items: center;
    }
}