.content_example{   
    margin-top: 30px;
    grid-area: content;
    
   display: grid;
   grid-template-columns: 1fr;
   
 
}
.example_item_list{
    background-color: white;
    width: 100%;
   
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr ;
    justify-items: center;
   

}
.example_item_main{
    font-size: 30px;
    font-weight: 800;
    color: rgb(149, 101, 101);
    border-radius: 10px;
    margin: 20px;
    width: 200px;
    height: 200px;
    background-color: #fff;
    border: 2px solid #6C5EFE;
    text-align: center;
    opacity: 0.5;
   
}

