.header{
    display:flex;
width: 100%;
height: 60px;
background-color: #6C5EFE;  
justify-content: center;
   align-items: center;
}
.navbar{
    display: flex;
  
}

.nav_logo{
   

    width: 170px;
 position: absolute;
    left:10px;
    top:10px;

}
.logo_item{  color: #fff;
    font-size: 24px;;
    text-decoration: none;
    
}
.nav_menu{
    display:flex;
    
 
}
.nav_ul{
    display: flex;
   
    
}

.nav_li{
    list-style: none;
margin: 20px;
}
.nav_link{
   align-self: center;
    color: white;
    text-decoration: none;
    font-size: 20px;
    font-weight: 300;
}


.hamb{
    display: none;
}

@media only screen and (max-width:768px){
    .logo_item{  color: #fff;
        font-size: 20px;;
        text-decoration: none;
        
    }
    
   
       .nav_ul{
      position: absolute;
        flex-direction: column;
        background-color: #fff;
        width: 50%;
        text-align: center;
        transition: 0.3s;
        box-shadow: 0 10px 27px rgba(0, 0, 0, 0.5);
        left: -100%;
        top: 4rem;
        }
        nav_ul.active{

        }

        .hamb{
            display: block;
            cursor: pointer;
        }
}
